import React from "react";
import "../src/App.css";

const Download = (data) => {
  let myString = JSON.stringify(data);
  let myString2 = myString.replace('{"data":', "");
  let myString3 = myString2 + "xd";
  let myString4 = myString3.replace("}xd", "");

  return (
    <React.Fragment>
      <a
        href={`data:text/json;charset=utf-8,${encodeURIComponent(myString4)}`}
        download="MLPTreeData.json"
      >
        <button className="download-button">Download Json</button>
      </a>
    </React.Fragment>
  );
};

export default Download;
